import React, {
  useCallback, useEffect, useMemo, useRef
} from 'react';
import {
  Box, HStack, Text, Image, Flex,
  // useToast
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { card } from '../types/card';
import style from './Card.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../hooks/store';
import { showToken } from '../store/middlewares/card';
import { apiErrorHandler, getValutId, loading } from '../utils/general';
import CardImg from '../assets/images/cards/card-dummy.svg';

type Props = {
    data: card,
    onReady?: (data: {
          show:any, headers: {
          'sd-show-token': string;
        }
    }) => void;
    showSecrets?: boolean
}

const Card:React.FC<Props> = React.memo(({
  data, showSecrets, onReady
}) => {
  const c = data;
  const { t } = useTranslation();
  // const toast = useToast();
  const dispatch = useAppAsyncDispatch();

  const numberRef = useRef<HTMLDivElement>(null);
  const cvvRef = useRef<HTMLDivElement>(null);

  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);
  const businessData = useAppSelector((store) => store.business.selectedBusiness, shallowEqual);
  const cardImg:string = useMemo(() => (c.cardType === 'physical' ? programConfig?.brand.cardArtPhysical : programConfig?.brand.cardArtVirtual), [c, programConfig]);
  const textColor = useMemo(() => (c.cardType === 'physical' ? programConfig?.brand.physicalCardTextColor : programConfig?.brand.virtualCardTextColor), [c, programConfig]);

  const vgsInit = useCallback(() => {
    loading(true);
    dispatch(showToken(data.id))
      .then((d) => {
        loading(true);
        const timer = setTimeout(() => {
          loading(false);
        }, 15000);

        const show = window.VGSShow.create(getValutId(), (state:any) => {
          if (state?.cvvIframe?.revealed === true && state?.cardNoIframe?.revealed === true) {
            clearTimeout(timer);
            loading(false);
          }
        });

        const headers = { 'sd-show-token': d.showToken };

        /**
         * card number
         */
        const cardNoiframe = show.request({
          name: 'cardNoIframe',
          method: 'GET',
          headers,
          path: `/v1/card/${data.id}/show`,
          jsonPathSelector: 'cardNumber',
          serializers: [show.SERIALIZERS.replace('(\\d{4})(\\d{4})(\\d{4})(\\d{4})', '$1 $2 $3 $4')],
        });

        cardNoiframe.render(numberRef.current, {
          color: textColor, fontSize: '15px', height: '20px', overflow: 'hidden', fontWeight: '700'
        });

        /**
         * cvv
         */
        const iframe = show.request({
          name: 'cvvIframe',
          method: 'GET',
          headers,
          path: `/v1/card/${data.id}/show`,
          jsonPathSelector: 'cvv'
        });
        iframe.render(cvvRef.current, {
          color: textColor, fontSize: '13px', display: 'block', height: '16px', overflow: 'hidden'
        });

        if (onReady) {
          onReady({ show, headers });
        }
      })
      .catch(apiErrorHandler)
      .finally(() => loading(false));
  }, [data]);

  useEffect(() => {
    if (showSecrets) {
      const vgsScript = document.createElement('script');
      vgsScript.id = 'vgs-script';
      vgsScript.crossOrigin = 'anonymous';
      const orgId = process.env.REACT_APP_VGS_ORG_ID;
      vgsScript.src = `https://js.verygoodvault.com/vgs-show/1.3/${orgId}.js`;
      vgsScript.type = 'text/javascript';
      vgsScript.onload = vgsInit;
      document.head.append(vgsScript);
    }

    return () => {
      window.VGSShow = undefined;
      document.getElementById('vgs-script')?.remove();
    };
  }, [showSecrets]);

  return (
    <Box opacity={c.cardStatus === 'inactive' ? '0.5' : '1'} className={`${style.card_image_info} ${style.cardList__card_image_info_white}`}>
      <Image className={style.card_image} alt="logo" src={cardImg} fallbackSrc={CardImg} data-testid="card-img" />
      <Box className={style.card_details}>
        <Box pt="14px" mb="auto">
          <Text color={textColor} fontWeight="500" fontSize="10px">{t(`cards_list.${c.cardType}`)}</Text>
          <Text color="#A1A1A1" fontSize="10px">Limited Use</Text>
        </Box>
        {showSecrets ? (
          <Flex>
            <Box className={style['secret-wrapper']} height="20px" width="60%" overflow="hidden" ref={numberRef} mb="12px" />

          </Flex>
        )
          : (
            <Text color={textColor} fontWeight="700" fontSize="15px" pb="8px">
              **** **** ****
              {' '}
              {c.last4}
            </Text>
          ) }
        <HStack>
          <Box alignItems="flex-start" mr="40px">
            <Text color="#A1A1A1" fontSize="10px">{t('cards_list.expDate')}</Text>
            <Text color={textColor} fontSize="13px">
              {c.expiryMonth}
              /
              {c.expiryYear}
            </Text>
          </Box>
          <Box alignItems="flex-start">
            <Text color="#A1A1A1" fontSize="10px">{t('cards_list.cvv')}</Text>
            {showSecrets ? <Box className={style['secret-wrapper']} height="20px" width="50px" ref={cvvRef} />
              : (
                <Text color={textColor} fontSize="13px">***</Text>
              )}
          </Box>
        </HStack>
        <Box mt="auto">
          <Text color={textColor} fontWeight="700" fontSize="9px">
            {c.embossingPerson !== '' ? c.embossingPerson : c.cardholder.name}
          </Text>
          <Text color={textColor} fontWeight="700" fontSize="9px">
            {c.embossingBusiness !== '' ? c.embossingBusiness : businessData?.legalName}
          </Text>
        </Box>
      </Box>
    </Box>
  );
});

export default Card;
